
import { ref } from "vue";
import axios from "axios";
import { useRouter } from "vue-router";
import Alert from "../components/Alert.vue";
export default {
  components: { Alert },
  setup() {
    const router = useRouter();
    const alert = ref<string>("");

    interface Customer {
      name: string;
      phone: string;
      email: string;
      education: string;
      graduationschool: string;
      profession: string;
      profile: string;
    }
    const customer = ref<Customer>({
      name: "",
      phone: "",
      email: "",
      education: "",
      graduationschool: "",
      profession: "",
      profile: "",
    });

    const addCustomer = async () => {
      if (
        !customer.value.name ||
        !customer.value.phone ||
        !customer.value.email
      ) {
        alert.value = "请添写对应的信息";
        // console.log("请添加对应的信息");
      } else {
        let newCustomer = {
          name: customer.value.name,
          phone: customer.value.phone,
          email: customer.value.email,
          education: customer.value.education,
          graduationschool: customer.value.graduationschool,
          profession: customer.value.profession,
          profile: customer.value.profile,
        };

        const res = await axios.post(
          "http://localhost:3000/users",
          newCustomer
        );
        // console.log(res);
        router.push({ path: "/", query: { alert: "用户信息添加成功" } });
      }
    };

    return { customer, addCustomer, alert };
  },
};
